"use client"

import type React from "react"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"

export function LoginForm() {
  const router = useRouter()
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const [error, setError] = useState("")
  const [isLoading, setIsLoading] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setIsLoading(true)

    const mockUsers = {
      admin: { password: "admin123", role: "admin", name: "System Admin" },
      hod: { password: "hod123", role: "hod", name: "Dr. Department Head" },
      faculty: { password: "faculty123", role: "faculty", name: "Prof. Faculty Member" },
      placement: { password: "placement123", role: "placement", name: "Placement Officer" },
    }

    // Simulate API call delay
    await new Promise((resolve) => setTimeout(resolve, 500))

    const studentLogins = JSON.parse(localStorage.getItem("studentLogins") || "{}")
    const facultyLogins = JSON.parse(localStorage.getItem("facultyLogins") || "{}")
    const staffLogins = JSON.parse(localStorage.getItem("staffLogins") || "[]")

    let user = mockUsers[username as keyof typeof mockUsers]
    let isStudent = false
    let isFaculty = false
    let isStaff = false

    if (!user) {
      const staffUser = staffLogins.find((s: any) => s.username === username)
      if (staffUser && staffUser.password === password) {
        user = staffUser
        isStaff = true
        console.log("[v0] Staff login successful:", staffUser)
      }
    }

    // Check if it's a student login
    if (!user && studentLogins[username]) {
      const studentLogin = studentLogins[username]
      console.log("[v0] Found student login:", studentLogin)
      console.log("[v0] Comparing passwords - entered:", password, "stored:", studentLogin.password)

      if (studentLogin.password === password) {
        user = studentLogin
        isStudent = true
        console.log("[v0] Password match! Student authenticated")
      } else {
        console.log("[v0] Password mismatch!")
      }
    }

    // Check if it's a faculty login
    if (!user && facultyLogins[username]) {
      const facultyLogin = facultyLogins[username]
      console.log("[v0] Found faculty login:", facultyLogin)
      console.log("[v0] Comparing passwords - entered:", password, "stored:", facultyLogin.password)

      if (facultyLogin.password === password) {
        user = facultyLogin
        isFaculty = true
        console.log("[v0] Password match! Faculty authenticated")
      } else {
        console.log("[v0] Password mismatch!")
      }
    }

    if (user && (isStudent || isFaculty || isStaff || user.password === password)) {
      // Store user info in localStorage
      const userData = {
        username,
        role: user.role,
        name: user.name,
        ...(isStudent && { studentId: user.studentId }),
        ...(isFaculty && { facultyId: user.facultyId, email: user.email }),
        ...(isStaff && { staffId: user.id, email: user.email, department: user.department }),
      }

      localStorage.setItem("user", JSON.stringify(userData))
      console.log("[v0] Login successful, redirecting to:", `/dashboard/${user.role}`)

      // Redirect based on role
      router.push(`/dashboard/${user.role}`)
    } else {
      console.log("[v0] Login failed - invalid credentials")
      setError("Invalid username or password")
      setIsLoading(false)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Sign In</CardTitle>
        <CardDescription>Enter your credentials to access the portal</CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="space-y-2">
            <Label htmlFor="username">Username</Label>
            <Input
              id="username"
              type="text"
              placeholder="Enter your username or roll number"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
              disabled={isLoading}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="password">Password</Label>
            <Input
              id="password"
              type="password"
              placeholder="Enter your password or date of birth"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              disabled={isLoading}
            />
          </div>

          <Button type="submit" className="w-full" disabled={isLoading}>
            {isLoading ? "Signing in..." : "Sign In"}
          </Button>

          <div className="mt-4 p-3 bg-muted rounded-md text-xs text-muted-foreground">
            <p className="font-medium mb-2">Demo Credentials:</p>
            <div className="space-y-1">
              <p>Admin: admin / admin123</p>
              <p>HOD: hod / hod123</p>
              <p>Faculty: faculty / faculty123</p>
              <p>Placement: placement / placement123</p>
              <p className="mt-2 font-medium">Students: Use Roll Number / Date of Birth (YYYY-MM-DD)</p>
              <p className="font-medium">Custom Users: Use credentials from admin panel</p>
            </div>
          </div>
        </form>
      </CardContent>
    </Card>
  )
}
